@import "variables";
@import "slick"; 

:focus {
  outline:0;
}
.cathay-tab-comp{
  .ctc-bg{
    position: absolute;
    background-repeat: no-repeat;
    
    width: 100%; 
    height: 100%;
    top:0;
    left:0;
    right: 0;
    bottom:0;
    pointer-events: none;
    
    &.ctc-bg--img{
      background-size: contain;
    }
    &.ctc-bg--top{
      background-position: center top;
    }
    &.ctc-bg--center{
      background-position: center center;
    }
    &.ctc-bg--bottom{
      background-position: center bottom;
    }
  }
  .ctc-title{
    position:relative;
  }
  .ctc-title + .ctc-tab{
    margin-top: 15px;
  }
  .ctc-title.d-none + .ctc-tab{
    margin-top: 0;
  }
  .ctc-title + .ctc-tab.d-none + .ctc-tabpane{
    margin-top: 15px;
  }

  .ctc-title.d-none + .ctc-tab.d-none + .ctc-tabpane{
    margin-top: 0px;
  }
  .cathay-carosel + .ctc-btn{
    margin-top: 15px;
  }
}

.cathay-carosel{
  font-size:16px;
  line-height: 1;
  // font-family:Arial,Verdana,sans-serif,"Microsoft JhengHei";
  margin:0 auto;
  width: auto;
  position: relative;
  * {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  @import "../common/reset"; 

  .#{$prefix}title{
    font-size:1.5em;
  }
  .#{$prefix}subtitle{
    font-size:1.25em;
  }
  .#{$prefix}content{
    font-size:1em;
  }

  .#{$prefix}slider{
    .slick-slide{
      display: block;  
    }

    img{
      border:0;
      display: block;
      width: 100%;
    }
  }
  .slick-slide img{
    border:0;
    display: block;
    width: 100%;
    // padding:2%;
  }

  .slick-slide img.slick-loading {
    border: 0;
  }

  .slick-slider {
    margin-bottom: 0px;
  }
  
  .#{$prefix}txt-wrap{
    display: none;
  }

  .#{$prefix}more{
    display: none;
  }
  //gutter

  &.#{$prefix}-gutter{
    
      @media (min-width: 481px){
       .slick-slide {
          margin: 0 5px;
        }
        /* the parent */
        .slick-list {
          margin: 0 -5px;
        }
      }

    
  }

}

