@import "variables";

.cathay-carosel{
/* Slider */
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}

/* Slider */

.slick-list {
    .slick-loading & {
        background: #fff slick-image-url("ajax-loader.gif") center center no-repeat;
    }
}


/* Arrows */
.slick-prev,.slick-next{
    position: absolute;
    display: block;
    width: 40px;
    height: 40px;
    border: 2px solid $slick-arrows-color;
    border-radius: 50%;
    background-color: transparent;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    outline: none;
    top: calc(50% - 20px);

    transition: all .15s ease-in-out;

    &:before {
        content:"";
        display: inline-block;
        margin-top:2px;
        width: 10px;
        height: 10px;
        border-top: 2px solid $slick-arrows-color;
        border-right: 2px solid $slick-arrows-color;
    }
    &:hover{
        outline: none;
        transform: scale(1.1);
    }
    &.slick-disabled{
        opacity: $slick-opacity-not-active;
    }
    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }
}

.slick-prev {
    left: 15px;
    z-index: 100;
    &:before {
        margin-left: 3px;
        -moz-transform: rotate(-135deg);
        -webkit-transform: rotate(-135deg);
        transform: rotate(-135deg);
    }
}

.slick-next {
    right: 15px;
    z-index: 101;
    &:before {
        margin-left: -3px;
        -moz-transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
    }
}

/* Dots */


.slick-dots {
    position: absolute;
    bottom: 5px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        margin: 0 10px;
        padding: 0;
        cursor: pointer;
        button {
            border-top-left-radius: 50px;
            border-bottom-left-radius: 50px;
            border-top-right-radius: 50px;
            border-bottom-right-radius: 50px;
            border:0;
            background: $slick-dot-color;
            display: block;
            width: 100%;
            height: 100%;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 4px;
            cursor: pointer;
            // transition: all .15s ease-in-out;
            &:hover, &:focus {
                outline: none;
                border-color:$slick-dot-border-hover;
                background: $slick-dot-color-hover;
            }
        }
        &.slick-active button {
            border-color:$slick-dot-border-color-active;
            background: $slick-dot-color-active;
        }
    }
}
}